<template>
  <div>
    <v-chart :forceFit="true" :height="height" :data="data">
      <v-coord type="rect" direction="LB" />
      <v-tooltip />
      <v-axis dataKey="country" :label="label" />
      <v-bar position="country*population" />
    </v-chart>
  </div>
</template>

<script>
  const DataSet = require('@antv/data-set');

  const sourceData = [
    { country: '首拨完成', population: 40 },
    { country: '预约完成', population: 21 },
    { country: '成功数量', population: 17 },
    { country: '失败数量', population: 13 },
    { country: '无效名单', population: 9 },
  ];

  const dv = new DataSet.View().source(sourceData);
  dv.transform({
    type: 'sort',
    callback(a, b) {
      return a.population - b.population > 0;
    },
  });
  const data = dv.rows;

  const label = { offset: 12 };

  export default {
    data() {
      return {
        data,
        height: 400,
        label: label,
      };
    }
  };
</script>


