<template>
  <div class="page-header-index-wide">
    <a-row style="margin-left: -12px;margin-right: -12px;">
        <a-col :sm="24" :md="12" :xl="6" style="padding-left: 12px;padding-right: 12px;margin-bottom: 24px;">
          <chartCard :loading="loading" title="成交总额" total="￥ 189,345">
            <a-tooltip title="指标说明" slot="action">
              <a-icon type="info-circle-o" />
            </a-tooltip>
            <div>
              <trend flag="up" style="margin-right: 16px;">
                <span slot="term">周同比</span> 12%
              </trend>
              <trend flag="down">
                <span slot="term">日同比</span> 11%
              </trend>
            </div>
            <div slot="footer">日均成交额<span> ￥234.56</span></div>
          </chartCard>
        </a-col>
        <a-col :sm="24" :md="12" :xl="6" style="padding-left: 12px;padding-right: 12px;margin-bottom: 24px;">
          <chartCard :loading="loading" title="拨号量" total="8,888">
            <a-tooltip title="指标说明" slot="action">
              <a-icon type="info-circle-o" />
            </a-tooltip>
            <div v-if="showChart">
              <miniArea/>
            </div>
            <div slot="footer">日拨号量<span> 123,4</span></div>
          </chartCard>
        </a-col>
        <a-col :sm="24" :md="12" :xl="6" style="padding-left: 12px;padding-right: 12px;margin-bottom: 24px;">
          <chartCard :loading="loading" title="拨号成功数" total="6,666">
            <a-tooltip title="指标说明" slot="action">
              <a-icon type="info-circle-o" />
            </a-tooltip>
            <div v-if="showChart">
              <miniBar/>
            </div>
            <div slot="footer">转化率 <span>60%</span></div>
          </chartCard>
        </a-col>
        <a-col :sm="24" :md="12" :xl="6" style="padding-left: 12px;padding-right: 12px;margin-bottom: 24px;">
          <chartCard :loading="loading" title="运营活动效果" total="73%">
            <a-tooltip title="指标说明" slot="action">
              <a-icon type="info-circle-o" />
            </a-tooltip>
            <div>
              <miniProgress color="rgb(19, 194, 194)" :target="80" :percentage="78" height="8px" />
            </div>
            <div slot="footer">
              <trend flag="down" style="margin-right: 16px;">
                <span slot="term">同周比</span> 12%
              </trend>
              <trend flag="up">
                <span slot="term">日环比</span> 80%
              </trend>
            </div>
          </chartCard>
        </a-col>
      </a-row>

    <a-card v-if="showChart" :loading="loading" :bordered="false" :body-style="{padding:loading?'24px':0,transition:'padding 0s'}">
      <div class="salesCard">
        <a-tabs default-active-key="1" size="large" :tab-bar-style="{marginBottom: '24px', paddingLeft: '24px'}">
          <div class="extra-wrapper" v-if="isDesktop()" slot="tabBarExtraContent">
            <div class="extra-item">
              <a>今日</a>
              <a>本周</a>
              <a>本月</a>
              <a>本年</a>
            </div>
            <range-picker :style="{width: '256px'}" />
          </div>
          <a-tab-pane loading="true" tab="个人成交量" key="1">
            <a-row>
              <a-col :xl="16" :lg="12" :md="12" :sm="24" :xs="24">
                <bar title="个人成交量" />
              </a-col>
              <a-col :xl="8" :lg="12" :md="12" :sm="24" :xs="24">
                <rank-list title="个人成交量排行榜（组内）" :list="rankList"/>
              </a-col>
            </a-row>
          </a-tab-pane>
          <a-tab-pane tab="小组成交量" key="2">
            <a-row>
              <a-col :xl="16" :lg="12" :md="12" :sm="24" :xs="24">
                <bar :style="{ padding: '0 0 24px 24px' }" title="小组成交量" />
              </a-col>
              <a-col :xl="8" :lg="12" :md="12" :sm="24" :xs="24">
                <rank-list title="小组成交量排行榜" :list="groupRankList"/>
              </a-col>
            </a-row>
          </a-tab-pane>
        </a-tabs>
      </div>
    </a-card>

    <a-row :gutter="12">
      <a-col :xl="12" :lg="24" :md="24" :sm="24" :xs="24">
        <a-card v-if="showChart" :loading="loading" :bordered="false" title="日检视表" :style="{ marginTop: '24px' }">
          <a-dropdown :trigger="['click']" placement="bottomLeft" slot="extra">
            <a class="ant-dropdown-link" href="#">
              <a-icon type="ellipsis" />
            </a>
            <a-menu slot="overlay">
              <a-menu-item>
                <a href="javascript:;">操作一</a>
              </a-menu-item>
              <a-menu-item>
                <a href="javascript:;">操作二</a>
              </a-menu-item>
            </a-menu>
          </a-dropdown>
          <basic-bar />
        </a-card>
      </a-col>
      <a-col :xl="12" :lg="24" :md="24" :sm="24" :xs="24">
        <a-card :loading="loading" :bordered="false" title="今日业绩" :style="{ marginTop: '24px' }">
          <a-dropdown :trigger="['click']" placement="bottomLeft" slot="extra">
            <a class="ant-dropdown-link" href="#">
              <a-icon type="ellipsis" />
            </a>
            <a-menu slot="overlay">
              <a-menu-item>
                <a href="javascript:;">操作一</a>
              </a-menu-item>
              <a-menu-item>
                <a href="javascript:;">操作二</a>
              </a-menu-item>
            </a-menu>
          </a-dropdown>
          <p>card content</p>
          <p>card content</p>
          <p>card content</p>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
  import ChartCard from '@/components/Card/ChartCard'
  import ACard from 'ant-design-vue/lib/card'
  import ADropdown from 'ant-design-vue/lib/dropdown'
  import AMenu from 'ant-design-vue/lib/menu'
  import ARow from 'ant-design-vue/lib/row'
  import ACol from 'ant-design-vue/lib/col'
  import ATooltip from 'ant-design-vue/lib/tooltip'
  import AIcon from 'ant-design-vue/lib/icon'
  import ATabs from 'ant-design-vue/lib/tabs'
  import DatePicker from 'ant-design-vue/lib/date-picker'
  import MiniArea from '@/components/Chart/MiniArea'
  import MiniBar from '@/components/Chart/MiniBar'
  import MiniProgress from '@/components/Chart/MiniProgress'
  import RankList from '@/components/Chart/RankList'
  import Bar from '@/components/Chart/Bar'
  import BasicBar from '@/components/Chart/BasicBar'
  import Trend from '@/components/Trend'
  import {mixin, mixinDevice} from '@/utils/mixin.js'


  const ATabPane = ATabs.TabPane
  const {RangePicker} = DatePicker
  const AMenuItem = AMenu.Item

  const rankList = []
  for (let i = 0; i < 7; i++) {
    rankList.push({
      name: '座席 600' + (i+1),
      total: 1234 - i * 100
    })
  }
  const groupRankList = []
  for (let i = 0; i < 7; i++) {
    groupRankList.push({
      name: '搬砖' + (i+1) + '组',
      total: 1234 - i * 100
    })
  }

  export default {
    name: 'Analysis',
    components: {
      ATooltip,
      ARow,
      ACol,
      AIcon,
      ATabs,
      ATabPane,
      ChartCard,
      MiniArea,
      MiniBar,
      MiniProgress,
      RankList,
      Bar,
      BasicBar,
      Trend,
      RangePicker,
      ACard,
      AMenu,
      AMenuItem,
      ADropdown
    },
    mixins:[mixin,mixinDevice],
    data() {
      return {
        loading: true,
        showChart:false,//通过v-if和nextTick延迟加载chart，使得获取正确的宽度
        rankList,
        groupRankList,
      }
    },
    created() {
      setTimeout(() => {
        this.loading = !this.loading
      }, 1500)
    },
    mounted() {
      // this.getStatisticsPanelData();
      this.$nextTick(()=>{
        this.showChart = true
      })
    },
  }
</script>

<style lang="scss" scoped>
  .extra-wrapper {
    line-height: 55px;
    padding-right: 24px;

    .extra-item {
      display: inline-block;
      margin-right: 24px;

      a {
        margin-left: 24px;
      }
    }
  }
</style>


